.tu-radio {
  color: #9baacf;
  font-weight: 500;
  line-height: 1;
  position: relative;
  cursor: pointer;
  display: inline-block;
  white-space: nowrap;
  outline: none;
  font-size: 14px;
  margin-right: 30px;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none; }
  .tu-radio.is-checked .tu-radio__inner {
    box-shadow: inset 2px 2px 4px #c8d0e7, inset -2px -2px 5px #ffffff; }
    .tu-radio.is-checked .tu-radio__inner::after {
      background: #6d5dfc; }
  .tu-radio__input {
    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: middle; }
  .tu-radio__inner {
    display: inline-block;
    box-sizing: border-box;
    width: 22px;
    height: 22px;
    box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    position: relative;
    cursor: pointer;
    border-radius: 50%; }
    .tu-radio__inner:hover::after {
      background: #6d5dfc; }
    .tu-radio__inner::after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%) scale(1);
      width: 36%;
      height: 36%;
      background: #9baacf;
      border-radius: 50%;
      transition: 0.3s ease; }
  .tu-radio__original {
    opacity: 0;
    outline: none;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0; }
  .tu-radio__label {
    font-size: 14px;
    padding-left: 8px; }
